
#logo {
    text-align: center;
    display: block;
    padding: 32px 16px 24px 16px;
    font-size: 3em
}
#logo-mobile {
    text-align: center;
    display: block;
    padding: 0;
    font-size: 3em;
    padding: 1%;
}
.slideout-menu {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 256px;
    height: 100vh;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    z-index: 0;
    display: none;
    background-color: #1D1F20;
    color: white;
}
.slideout-menu-left {
    left: 0;
}
.slideout-menu-right {
    right: 0;
}
.slideout-panel {
    color: white;
    position: relative;
    z-index: 1;
    will-change: transform;
}
.slideout-open,
.slideout-open body,
.slideout-open .slideout-panel {
    overflow: hidden;
}
.slideout-open .slideout-menu {
    display: block;
}



